header#header {
	background: #1C252C;
	color: #FFD270;
	font-family: "Roboto Condensed", "Helvetica Neue", "Helvetica";
	font-weight: 700;
	letter-spacing: -0.04em;
	text-align: center;
	font-size: 70px;
	line-height: 60px;
	padding: 50px 30px;
	margin: 0 0 30px 0;
	h1 {
		color: #FFD270;
		font-size: 70px;
		line-height: 90px;
		font-weight: 700;
		&::after {
			background: rgba(255, 210, 112, 0.75);
		}
	}
	h2 {
		color: rgba(255, 210, 112, 0.5);
	}
}
footer#footer {
	background: #DDD;
	color: #888;
	padding: 30px 0;
	margin-top: 30px;
	a {
		color: #888;
		&:visited {
			color: #888;
		}
	}
}
label {
	font-weight: bold;
	display: block;
	margin-bottom: 5px;
}
p.help {
	color: #999;
}
#toggleDropify {
	font-size: 12px;
	text-transform: uppercase;
	background: #DDD;
	color: #888;
	font-weight: bold;
	border: 0;
	padding: 6px 10px;
	border-radius: 4px;
	margin-left: 10px;
	-webkit-transition: background 0.1s linear;
	transition: background 0.1s linear;
	&:hover {
		background: #EEE;
	}
}
